// breakpoint

@media (min-width: 64em) {
  // style goes here
}

// 640px, 1024px, 1400px
$breakpoint-up: (
  "medium": "40em",
  "large": "64em",
  "xlarge": "87.5em",
);

// 639px, 1023px, 1399px
$breakpoint-down: (
  "small": "39.9375em",
  "medium": "63.9375em",
  "large": "87.4375em",
);

@mixin breakpoint-up($size) {
  @media screen and (min-width: map-get($breakpoint-up, $size )) {
    @content;
  }
}

@mixin breakpoint-down($size) {
  @media screen and (max-width: map-get($breakpoint-down, $size )) {
    @content;
  }
}
