.page{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    &__banner{
        background-color: aqua;
    }

    .card{
        flex: 1 0 14rem;
    }
    
    &__main-section{
        background-color: #e8efe0;
        display: flex;
        flex-direction: column;
        max-width: 100%;
        overflow: hidden;
    }

    &__title{
            font-size: $font-lg;
            font-weight: 700;
            margin-bottom: 0.6rem;
            color: $mainColor;
    }


    &__image{
        width: 100%;
        flex: 0 0 16rem;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        border-radius: 0.3125rem;
    }

    &__content{
        display: flex;
        flex-direction: column;
        height: 100%;
        padding: 1rem;
        
    }

    @include breakpoint-up(medium){
        &__image{
            flex: 0 0 28.75rem;
        }
    }

    @include breakpoint-down(medium){

        &__title{
            font-size: $font-med;
        }

        &__main-section{
           padding-left: 0.5rem;
        }

        .container{
            justify-content: center;
            .col-8 {
                width: 100%;
            }

            .col-4 {
                width: 100%;
            }
            
            .row{
            display: flex;
            flex-wrap: wrap;
            flex-direction: column;

            }
        }

    }
}

.single .related-posts-after-content .related-thumb {
    display:inline-block;
    width: 32%;
    }