.has-fade {
  visibility: hidden;
}

@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  1% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 200ms ease-in-out forwards;
}

@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1;
  }
  99% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
.fade-out {
  animation: fade-out 200ms ease-in-out forwards;
}

@keyframes breaking-news {
  0% {
    margin-top: -400px;
  }
  10% {
    margin-top: -400px;
  }
  20% {
    margin-top: -320px;
  }
  25% {
    margin-top: -320px;
  }
  35% {
    margin-top: -240px;
  }
  40% {
    margin-top: -240px;
  }
  50% {
    margin-top: -160px;
  }
  55% {
    margin-top: -160px;
  }
  65% {
    margin-top: -80px;
  }
  70% {
    margin-top: -80px;
  }
  80% {
    margin-top: 0;
  }
  99.99% {
    margin-top: 0;
  }
  100% {
    margin-top: -400px;
  }
}
@-webkit-keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
