.header {
  position: relative;
  z-index: 3;
}
.header.open .header__toggle > span:first-child {
  transform: rotate(45deg);
}
.header.open .header__toggle > span:nth-child(2) {
  opacity: 0;
}
.header.open .header__toggle > span:last-child {
  transform: rotate(-45deg);
}
.header .overlay {
  opacity: 0;
  position: fixed;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  background-image: linear-gradient(#2d314d, transparent);
}
.header nav {
  position: relative;
  padding-top: 1.0625rem;
  padding-bottom: 1.0625rem;
}
.header__logo img {
  width: 8.8125rem;
  height: 3.375rem;
}
.header__toggle > span {
  display: block;
  width: 1.625rem;
  height: 0.125rem;
  background-color: #2d314d;
  transition: all 300ms ease-in-out;
  transform-origin: 0.1875rem 0.0625rem;
}
.header__toggle > span:not(:last-child) {
  margin-bottom: 0.3125rem;
}
.header__menu {
  position: absolute;
  z-index: 99;
  width: calc(100% - 3rem);
  left: 50%;
  transform: translateX(-50%);
  background: #ffffff;
  margin-top: 1.5rem;
  padding: 1.625rem;
  border-radius: 0.3125rem;
}
.header__menu ul li a {
  display: block;
  padding: 0.625rem;
  color: #2d314d;
  text-align: center;
}
.header__links ul li:not(:first-child) {
  margin-right: 2rem;
}
.header__links ul li a {
  position: relative;
  font-size: 0.875rem;
  color: #9698a6;
  transition: 300ms ease-in-out;
}
.header__links ul li a::before {
  content: "";
  position: absolute;
  display: block;
  height: 0.3125rem;
  left: 0;
  right: 0;
  bottom: -1.875rem;
  background: linear-gradient(to right, #31d35c, #00cecb);
  opacity: 0;
  transition: opacity 300ms ease-in-out;
}
.header__links ul li a:hover {
  color: #2d314d;
}
.header__links ul li a:hover::before {
  opacity: 1;
}

