.search-overlay {
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 110;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: linear-gradient(#2d314d, transparent);
  visibility: hidden;
  opacity: 0;
  transform: scale(1.09);
  transition: opacity 0.3s, transform 0.3s, visibility 0.3s;
  box-sizing: border-box;
}
.search-overlay p {
  padding-top: 15px;
}
.search-overlay .event-summary p {
  padding-top: 0;
}
.search-overlay .event-summary {
  margin-bottom: 0;
  margin-top: 20px;
}
body.admin-bar .search-overlay {
  top: 32px;
}
.search-overlay__top {
  background-color: rgba(0, 0, 0, 0.12);
}
.search-overlay__icon {
  margin-left: 10px;
  font-size: 1.8rem;
  color: darkorange;
}
.search-overlay--active {
  visibility: visible;
  opacity: 1;
  transform: scale(1);
}
.search-overlay__section-title {
  margin: 30px 0 1px 0;
  font-weight: 400;
  color: blue;
  font-size: 2rem;
  padding: 15px 0;
  border-bottom: 1px solid #CCC;
}
.search-overlay__close {
  position: absolute;
  top: 13px;
  left: 16px;
  font-size: 2.1rem;
  cursor: pointer;
  transition: all 0.3s;
  background-color: #FFF;
  color: darkorange;
  line-height: 0.7;
}
.search-overlay__close:hover {
  opacity: 1;
}
.search-overlay .one-half {
  padding-bottom: 0;
}

.search-term {
  width: 75%;
  box-sizing: border-box;
  border: none;
  padding: 15px 0;
  margin: 0;
  background-color: transparent;
  font-size: 1rem;
  font-weight: 300;
  outline: none;
  color: darkorange;
}

.body-no-scroll {
  overflow: hidden;
}

.search-trigger {
  display: none;
  color: #9698a6;
  position: relative;
  cursor: pointer;
  top: 1px;
  margin-right: 40px;
}

@media (min-width: 960px) {
  .search-trigger {
    display: inline-block;
    top: 3px;
  }
}
.search-trigger:hover {
  opacity: 0.65;
}

.site-header__search-trigger {
  cursor: pointer;
  color: #FFF;
  font-size: 1.2rem;
  position: absolute;
  top: 6px;
  right: 16px;
}

@media (min-width: 530px) {
  .site-header__search-trigger {
    top: 4px;
    font-size: 1.4rem;
  }
}
@media (min-width: 960px) {
  .site-header__search-trigger {
    display: none;
  }
}
.spinner-loader {
  margin-top: 45px;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  border: 0.25rem solid rgba(0, 0, 0, 0.2);
  border-top-color: black;
  -webkit-animation: spin 1s infinite linear;
  animation: spin 1s infinite linear;
}

.post-categories {
